@import "../../styles/animations/floating.scss";


.sectionWrapper {
    position: relative;
    flex: none;
    width: 90%;
    min-height: 80vh;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0 auto;

    .text {
        z-index: 1;
    }

    .sectionIcon {
        @extend .floating;
        position: absolute;
        right: 0;
        z-index: 0;
        border-radius: 100%;
        width: var(--ico-dimension);
    }

    &.reversed {
        text-align: right;

        .sectionIcon {
            left: 0
        }
    }
}

